import {AfterViewInit, Component, OnInit} from '@angular/core';


declare const echarts: any;
declare const $: any;
@Component({
  selector: 'app-about',
  templateUrl: './about.component.html',
  styleUrls: ['./about.component.css']
})
export class AboutComponent implements OnInit, AfterViewInit {


  tab_num = 1;
  map_chart: any;

  chart_c = 0;

  zoom = 0.8;

  constructor() { }

  ngOnInit() {

  }

  ngAfterViewInit() {
    if (this.tab_num == 1) {
      setTimeout( () => {
        this.set_map(0);
      }, 200);

    }
  }


  change_tab(num) {
    this.tab_num = num;
    if (num == 1) {
      setTimeout( () => {
        this.set_map(0);
      }, 200);

    }
  }

  change_map(option) {
    if (option == 1 && this.zoom < 1.8) {
      this.zoom = this.zoom + 0.1;
      this.set_map(0);
    }

    if (option == 0 && this.zoom > 0.8) {
      this.zoom = this.zoom - 0.1;
      this.set_map(0);
    }

  }


  set_map(real_data) {

    $('#map').height(($('#map').width() * 0.7));

    this.map_chart = echarts.init(document.getElementById('map'));

    let data = [
      {name: '', value: 658},
      {name: '', value: 582},
      {name: '', value: 822},
      {name: '', value: 684},
      {name: '', value: 779},
      {name: '', value: 738},
      {name: '', value: 251},
      {name: '', value: 182},
      {name: '', value: 183},
      {name: '', value: 181},
      {name: '', value: 192},
      {name: '', value: 193},
      {name: '', value: 199},
      {name: '', value: 272},
      {name: '', value: 273},
      {name: '', value: 281},
      {name: '', value: 292},
      {name: '', value: 203},
      {name: '', value: 201},
      {name: '', value: 212},
      {name: '', value: 213},
      {name: '', value: 211},
      {name: '', value: 312},
      {name: '', value: 323},
      {name: '', value: 324},
      {name: '', value: 325},
      {name: '', value: 331},
      {name: '', value: 332},
      {name: '', value: 333},
      {name: '', value: 341},
      {name: '', value: 342},
      {name: '', value: 451},
      {name: '', value: 452},
      {name: '', value: 463},
      {name: '', value: 464},
      {name: '', value: 465},
      {name: '', value: 466},
      {name: '', value: 467},
      {name: '', value: 471},
      {name: '', value: 572},
      {name: '', value: 573},
      {name: '', value: 574},
      {name: '', value: 575},
      {name: '', value: 541},
      {name: '', value: 542},
    ];

    let fake_data = 'Created By Chromie Wang';
    let geoCoordMap = {
      '':[121.48,31.22],
      '':[113.23,23.16],
      '':[114.07,22.62],
      '':[113.52,22.3],
      '':[116.46,39.92],
      '':[120.19,30.26],
      '':[101.718637,26.582347],
      '':[122.1,37.5],
      '':[117.93,40.97],
      '':[118.1,24.46],
      '':[115.375279,22.786211],
      '':[116.63,23.68],
      '':[124.37,40.13],
      '':[121.1,31.45],
      '':[103.79,25.51],
      '':[121.39,37.52],
      '':[119.3,26.08],
      '':[121.979603,39.627114],
      '':[120.45,36.38],
      '':[123.97,41.97],
      '':[102.52,24.35],
      '':[114.87,40.82],
      '':[113.57,37.85],
      '':[119.942327,37.177017],
      '':[120.1,30.86],
      '':[116.69,23.39],
      '':[120.95,31.39],
      '':[121.56,29.86],
      '':[110.359377,21.270708],
      '':[116.35,23.55],
      '':[122.41,37.16],
      '':[119.16,34.59],
      '':[120.836932,40.711052],
      '':[120.74,31.64],
      '':[113.75,23.04],
      '':[114.68,23.73],
      '':[119.15,33.5],
      '':[119.9,32.49],
      '':[108.33,22.84],
      '':[122.18,40.65],
      '':[114.4,23.09],
      '':[120.26,31.91],
      '':[120.75,37.8],
      '':[113.62,24.84],
      '':[98.289152,39.77313],
    };

    let convertData = function (data) {
      let res = [];
      for (let i = 0; i < data.length; i++) {
        let geoCoord = geoCoordMap[data[i].name];
        if (geoCoord) {
          res.push({
            name: data[i].name,
            value: geoCoord.concat(data[i].value)
          });
        }
      }
      return res;
    };

    if (real_data == 1) {
      this.chart_c ++;
      if (this.chart_c >= 10) {
        console.log(fake_data);
        this.chart_c = 0;
      }
      setTimeout(() => {this.chart_c = 0; }, 3000);

    }

      let option = {
      backgroundColor: '#fff',
      tooltip : {
        trigger: 'item',
        formatter: function(data){
          return  '\n' + data.name + ':' + data.value[2] + '';
        },
        /*   // formatter: `{a} <br/>{b}: {c} <br/> <br/>: ${data}`,*/
      },
      legend: {
        orient: 'vertical',
        y: 'bottom',
        x:'right',
        data:['pm2.5'],
        textStyle: {
          color: '#fff'
        }
      },
      geo: {
        map: 'china',
        // zoom: 0.8,
        zoom: this.zoom,
        scaleLimit: {
          min: 0.8,
          max: 1.8,
        },
        roam: false,
        label: {
          emphasis: {
            show: false
          }
        },
        itemStyle: {
          normal: {
            areaColor: '#97c3fa',
            borderColor: '#111'
          },
          emphasis: {
            areaColor: '#36c6d3'
          }
        }
      },
      series : [
        {
          name: '',
          type: 'scatter',
          coordinateSystem: 'geo',
          data: convertData(data),
          symbolSize: function (val) {
            return val[2] / 30;
          },
          label: {
            normal: {
              formatter: '{b}',
              position: 'right',
              show: false
            },
            emphasis: {
              show: true
            }
          },
          itemStyle: {
            normal: {
              color: '#2D3E5E'
            }
          }
        },
        {
          name: '',
          type: 'effectScatter',
          coordinateSystem: 'geo',
          data: convertData(data.sort(function (a, b) {
            return b.value - a.value;
          }).slice(0, 6)),
          symbolSize: function (val) {
            return val[2] / 30;
          },
          showEffectOn: 'render',
          rippleEffect: {
            brushType: 'stroke'
          },
          hoverAnimation: true,
          label: {
            normal: {
              formatter: '{b}',
              position: 'right',
              show: true
            }
          },
          itemStyle: {
            normal: {
              color: '#D8617A',
              shadowBlur: 10,
              shadowColor: '#333'
            }
          },
          zlevel: 1
        }
      ]
    };

    if (real_data != 1) {
      this.map_chart.setOption(option, true);
    }
  }



}



